import * as React from "react";
import { useForm, SubmitHandler } from "react-hook-form";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Typeahead } from "react-bootstrap-typeahead";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import { getPdfText } from "../helpers/pdf";
import { countries } from "../helpers/country";
import AppContext from "../context/AppContext";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";

enum GenderEnum {
  female = "female",
  male = "male",
  other = "other",
}

interface IFormInput {
  firstName: String;
  gender: GenderEnum;
}

export default function App() {
  const { t } = useTranslation();
  const { useEffect, useContext } = React;
  const [file, setFile] = React.useState(null);
  const [model, setModel] = React.useState({});

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
    console.log(model);
  };

  const context = useContext(AppContext);

  useEffect(() => {
    context.showLoading(true);
    Parse.Cloud.run("getMyProfile").then((app) => {
      if (app) {
        setModel({
          ...app.get("data"),
        });
      }

      context.showLoading(false);
    });
  }, [context.showLoading]);

  const isModelValid = () => {
    return model?.location?.length && model?.tags?.length && model.consent;
  };
  return (
    <>
      <Container className="">
        <Row>
          <Col>
            <div className="d-flex flex-column">
              <h3> {t("myprofile")}</h3>
              <div>{t("shareProfile")}</div>
            </div>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>First Name</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.name}
                onChange={(v) => setModelValue("name", v.target.value)}
              />
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Family Name</Form.Label>
              <Form.Control
                value={model.familyName}
                type="text"
                placeholder=""
                onChange={(v) => setModelValue("familyName", v.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Contact Number</Form.Label>
              <Row>
                <Col xs={3}>
                  <Form.Control
                    type="text"
                    placeholder="Country Code"
                    value={model?.telCode}
                    onChange={(e) => setModelValue("telCode", e.target.value)}
                  />
                </Col>
                <Col xs={9}>
                  <Form.Control
                    type="text"
                    placeholder=""
                    value={model?.contact}
                    onChange={(e) => setModelValue("contact", e.target.value)}
                  />
                </Col>
              </Row>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Email Address</Form.Label>
              <Row>
                <Col xs={6}>
                  <Form.Control
                    disabled={true}
                    type="text"
                    placeholder="Email"
                    value={Parse?.User?.current()?.get("email")}
                  />
                </Col>
              </Row>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form>
              <Button
                variant="primary"
                onClick={async () => {
                  context.showLoading(true);
                  await Parse.Cloud.run("updateProfile", {
                    model: {
                      data: model,
                    },
                  });
                  context.showLoading(false);
                }}
              >
                Update
              </Button>
            </Form>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export const Head: HeadFC = () => (
  <>
    <title></title>
    <script src="/assets/pdf/pdf.js" type="text/javascript" />
  </>
);
